@import url(https://fonts.googleapis.com/css?family=Proxima+Nova);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --light-blue-40: #f8f9fc;
    --light-blue-60: #eef2fa;
    --light-blue-80: #d9e3fa;
    --light-blue-100: #c3d3fa;

    --light-shadow: #f1f1f1;
    --menu-shadow: #b3b1b160;
    --dark-shadow: rgb(104, 104, 104);

    --light-grey-100: #cfd4d9;
    --light-grey-80: #dfe2e6;
    --light-grey-60: #eaecef;

    --dark-grey-100: #222529;
    --dark-grey-80: #353a3f;
    --dark-grey-60: #4A5056;
    --dark-grey-40: #6e757c;

    --iris-60: #a5a6f6;
    --iris-80: #7879f1;
    --iris-100: #5d5fef;

    --accent-pink-60: #F9A4B5;
    --accent-pink-100: #df5571;

    --logo-purple: #7175B7;
}

/* Define Light Theme Colours */
body {
    --bkg-color: var(--light-blue-60);
} 

/* Define Dark Theme Colours */
body.dark-theme {
    --bkg-color: rgb(35,37,40);
}

/* Define Body Elements */
.body {
    background-color: var(--bkg-color);
    position: absolute;
    width: 100%;
    min-height: 100%;
    font-family: "proxima-nova", sans-serif;
    font-size: 16px;
    padding-bottom: 20px;
}


nav {
    left: 0;
    top: 200;
    width: 100%;
    text-align: center;
    padding-top: 20px;
}

footer {
    position: fixed;
    height: 30px;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: white;
    text-align: center;
    padding-top: 5px;
}

.auth-form {
    width: 80%;
    max-width: 535px;
    padding: 0;
    display: block;
    margin: 80px auto;
}

.invisible-button {
    visibility: hidden;
}

button {
    padding: 12px 0px;
    width: 150px;
    border: none;
}

.primary-button {
    background-color: #a5a6f6;
    background-color: var(--iris-60);
    font-size: 14px;
    color: white;
    border-radius: 5px;
}
.primary-button.dark-theme {
    background-color: var(brown);
    font-size: 14px;
    color: white;
    border-radius: 5px;
}

.primary-button:hover:enabled {
    background-color: #7879f1;
    background-color: var(--iris-80);
    -webkit-animation: primaryButtonHover 1s;
            animation: primaryButtonHover 1s;
}

@-webkit-keyframes primaryButtonHover {
    from {
        background-color: #a5a6f6;
        background-color: var(--iris-60);
    }
    to {
        background-color: #7879f1;
        background-color: var(--iris-80);
    }
}

@keyframes primaryButtonHover {
    from {
        background-color: #a5a6f6;
        background-color: var(--iris-60);
    }
    to {
        background-color: #7879f1;
        background-color: var(--iris-80);
    }
}

button:disabled {
    background-color: #cfd4d9;
    background-color: var(--light-grey-100);
    box-shadow: none;
}

.secondary-button {
    background-color: #d9e3fa;
    background-color: var(--light-blue-80);
    font-size: 14px;
    color: white;
    border-radius: 5px;
}
.secondary-button.dark-theme {
    background-color: #d9e3fa;
    background-color: var(--light-blue-80);
    font-size: 14px;
    color: white;
    border-radius: 5px;
}

.secondary-button:hover:enabled {
    background-color: #c3d3fa;
    background-color: var(--light-blue-100);
    -webkit-animation: secondaryButtonHover 0.5s;
            animation: secondaryButtonHover 0.5s;
}

@-webkit-keyframes secondaryButtonHover {
    from {
        background-color: #d9e3fa;
        background-color: var(--light-blue-80);
    }
    to {
        background-color: #c3d3fa;
        background-color: var(--light-blue-100);
    }
}

@keyframes secondaryButtonHover {
    from {
        background-color: #d9e3fa;
        background-color: var(--light-blue-80);
    }
    to {
        background-color: #c3d3fa;
        background-color: var(--light-blue-100);
    }
}

.accent-button {
    background-color: #F9A4B5;
    background-color: var(--accent-pink-60);
    font-size: 14px;
    color: white;
    border-radius: 5px;
}

.accent-button:hover:enabled {
    background-color: #df5571;
    background-color: var(--accent-pink-100);
    -webkit-animation: accentButtonHover 0.5s;
