}

@-webkit-keyframes accentButtonHover {
    from {
        background-color: #F9A4B5;
        background-color: var(--accent-pink-60);
    }
    to {
        background-color: #df5571;
        background-color: var(--accent-pink-100);
    }
}

@keyframes accentButtonHover {
    from {
        background-color: #F9A4B5;
        background-color: var(--accent-pink-60);
    }
    to {
        background-color: #df5571;
