/* Define Light Theme Colours */
body {
    --bkg-color: var(--light-blue-60);
    --header-bg: white;
    --header-text: var(--iris-80);
} 

/* Define Dark Theme Colours */
body.dark-theme {
    --bkg-color: #232528;
    --header-bg: #353a3f;
    --header-text: #c3d3fa;
}

/* Define Body Elements */
header {
    top: 0;
    padding: 15px 15px;
}

.header-content {
    padding-left: 40px;
    display: flex;
    background-color: var(--header-bg);
    height: 50px;
    padding: 10px 15px;
    border-radius: 25px;
    justify-content: space-between;
    box-shadow: none;
}

.left-header a {
    font-size: 26px;
    line-height: 30px;
    color: var(--header-text);
}

.right-header {
    display: flex;
}

.icon {
    font-size: 26px;
    line-height: 26px;
    color: var(--light-blue-100);
    border-radius: 15px;
    margin-right: 30px;
}

.icon.on {
    color: var(--iris-80);
}

.profile-icon {
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: 2px solid var(--light-blue-100);
}

.icon:hover {
    cursor: pointer;
    color: var(--iris-80);
    -webkit-animation: textColorChange 1s;
            animation: textColorChange 1s;
}

.icon.off:light,
.icon.on:dark {
    transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: opacity 0.5s, transform 0.5s;
    transition: opacity 0.5s, transform 0.5s, -webkit-transform 0.5s;
    -webkit-transform: rotate(-180deg) scale(0.5);
            transform: rotate(-180deg) scale(0.5);
    opacity: 0;
}

.off:after,
.on:after {
    transition: opacity 0.5s, -webkit-transform 0.5s;
    transition: opacity 0.5s, transform 0.5s;
    transition: opacity 0.5s, transform 0.5s, -webkit-transform 0.5s;
    -webkit-transform: scale(0.5) rotate(-180deg) scale(1);
            transform: scale(0.5) rotate(-180deg) scale(1);
    opacity: 1;
}

.profile-icon.on {
    border: 2px solid var(--iris-80);
}

@-webkit-keyframes textColorChange {
    from {
        color: var(--iris-60);
    }
    to {
        color: var(--iris-80);
    }
}

@keyframes textColorChange {
    from {
        color: var(--iris-60);
    }
    to {
        color: var(--iris-80);
    }
}

.profile-icon:hover {
    cursor: pointer;
    border: 2px solid var(--iris-80);
    -webkit-animation: borderColorChange 1s;
            animation: borderColorChange 1s;
}

@-webkit-keyframes borderColorChange {
    from {
        border: 2px solid var(--iris-60);
    }
    to {
        border: 2px solid var(--iris-80);
    }
}

@keyframes borderColorChange {
    from {
        border: 2px solid var(--iris-60);
    }
    to {
        border: 2px solid var(--iris-80);
    }
}

.profile-dropdown {
    text-align: right;
    width: 30px;
}

.profile-dropdown > .dropdown {
    margin: 10px -180px;
}

.notification-dropdown > .dropdown {
    margin: 12px -180px;
}

.dropdown {
    font-size: 14px;
    border-radius: 5px;
    background-color: white;
    width: 200px;
    text-align: left;
    z-index: 1;
    -webkit-animation: growDown 300ms ease-in-out forwards;
            animation: growDown 300ms ease-in-out forwards;
    -webkit-transform-origin: top center;
            transform-origin: top center;
    color: var(--dark-grey-40);
}
.dropdown.dark-theme {
    font-size: 14px;
    border-radius: 5px;
    background-color: white;
    width: 200px;
    text-align: left;
    z-index: 1;
    -webkit-animation: growDown 300ms ease-in-out forwards;
            animation: growDown 300ms ease-in-out forwards;
    -webkit-transform-origin: top center;
            transform-origin: top center;
    color: var(--dark-grey-40);
}

@-webkit-keyframes growDown {
    0% {
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
    80% {
        -webkit-transform: scaleY(1.1);
                transform: scaleY(1.1);
    }
    100% {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
}

@keyframes growDown {
    0% {
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
    80% {
        -webkit-transform: scaleY(1.1);
                transform: scaleY(1.1);
    }
    100% {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
}

.dropdown a {
    color: var(--dark-grey-40);
}

.dropdown a:hover {
    text-decoration: none;
}

